.background-image{
  background-image: url(images/image1.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 88vh;
  }

 
  .content{
    display: flex;
    flex-direction: column;
    font-size: 30px;
    text-align: center;
    color:orangered;
    gap:20px;
    top: 65%;
    }

    

    .content h1{
     margin-top: 30%;
    }
  
    #about{
      background-color: blanchedalmond;
    }
    .h1{
      text-align: center;
      margin-top: 30px;
      font-size:40px;
    }

    .para{
      text-align: center;
      font-size: 20px;
      margin-top: 10px;
      padding: 20px;
      text-align: justify;
    }

  .about-image{
    width: 50%;
    height: 50%;
    display: flex;
    margin-left: 330px;
    margin-top: 40px;
    border: 2px solid violet;
    }

   .cards{
    width: 20%;
    height: 20%;
    display: flex;
    left: 30px;
   }


  #login{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #about{
    display: flex;

  }

  .cover{
    background-color: rgb(247, 247, 105);
    height: 34em;
    width: 30em;
    position: relative;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }

  #about{
    background-color: bisque;
  }

  #login{
    background-color: rgb(46, 248, 248);
  }

  input{
    border: none;
    background-color: aqua;
    height: 4em;
    width: 80%;
    border-radius: 0.25em;
    text-align: center;
    font-size: 15px;
    padding: 2em;
    }

  input:focus{
    outline: blue;
  }

  button{
    justify-content: center;
    display: flex;
    width: 40%;
    height: 3em;
    background-color: rgb(32,177,255);
    padding: 15px;
    font-size: 20px;
    transition: 0.5s;
    border-radius: 0.25em;
    cursor: pointer;
  }

  button:hover{
    transform: scale(1.25);
  }

  .container-form{
    margin-top: 50px;
    background-color: rgb(183, 119, 243);
    color: white;
    align-items: center;
    justify-content: center;
  }

  .header{
    text-align: center;
    padding: 30px;
    font-size: 30px;
  }
  .regis{
    border: none;
    background-color: rgb(90, 245, 123);
    height: 4em;
    width: 80%;
    border-radius: 0.25em;
    text-align: center;
    font-size: 15px;
    padding: 2em;
    margin-left: 10%;
    margin-top: 30px;
  }

  .submit{
    margin-top: 30px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
  }
 