*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar{
    display: flex;
    justify-content: space-around;
    background-color: blue;
    align-items: center;
    width: 100%;
    height: 75px;
    position: sticky;
}

.logo{
    color: white;
    font-size: 40px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-style: italic;
}

.nav-item{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-content: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 600;
    }

    .home,.about,.login,.registration{
       padding: 10px 30px;
        color: white;
        list-style: none;
        text-decoration: none;
        font-size: 20px;
    }


    .menu-icon{
        display: none;
    }

   @media screen and (max-width:780px){
    .logo{
        display: flex;
        top: 15px;
        position: absolute;
        left: 35;
    }

    .nav-item{
        display: none;
    }

    .nav-item-mob{
        position: absolute;
        display: block;
        list-style: none;
        background-color: aqua;
        left: 0;
        top: 75px;
        width: 100%;
        transition: all 0.5s ease-out;
    }
     
    .home,.about,.login,.registration{
        color: black;
        align-items: center;
        text-align: center;
        width: 100%;
        transition: all 0.5s ease;
    }

    .menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color: white;
        background-color: blue;
        cursor: pointer;
        top: 15px;
        right: 25px;
        border: none;
    }

    }
 
       
        
       
    
    
   



